import React, { useEffect, useState } from "react";
import ReactMapGL, { Marker } from "react-map-gl";
import mapboxgl from 'mapbox-gl/dist/mapbox-gl';
import MapboxWorker from 'mapbox-gl/dist/mapbox-gl-csp-worker';
import "./layout.css";
import LocationOnIcon from '@material-ui/icons/LocationOn';

function Map({ handlemapClose, longitude, latitude }) {
  mapboxgl.workerClass = MapboxWorker;
  const [viewport, setViewport] = useState({
    latitude: 0,
    longitude: 0,
    zoom: 10,
    height: "500px",
    width: "600px",
    pitch: 50,
  });
  useEffect(() => {
    setViewport({ latitude: (latitude), longitude: (longitude), zoom: 8,
    height: "500px",
    width: "600px",
    pitch: 50, });
  }, [latitude,longitude]);
  console.log(latitude, longitude)

  return (
    <div className="mainMap">

      {latitude && longitude ?(

       <ReactMapGL
        mapboxApiAccessToken={
          "pk.eyJ1Ijoid2VibG9vcCIsImEiOiJja21pdXBrMWQwbDBtMndzMjZnZ3FicTIxIn0.sTsSShxZ2LqjqUWb_akSyw"
        }
        mapStyle="mapbox://styles/mapbox/streets-v11"
        {...viewport}
        onViewportChange={(newView) => setViewport(newView)}
      >
        <Marker longitude={longitude} latitude={latitude}>
          <div className="marker">
            <LocationOnIcon />
          </div>
        </Marker>
      </ReactMapGL>
      )
      :null}
      
      <div className="btnClose">
        <button onClick={handlemapClose}>close</button>
      </div>
    </div>
  );
}

export default Map;
